.button{
  font-size: 20px;
  width: 225px;
  border-radius: 10px;
  border: none;
  margin: 2%;
  padding: 1% 0;
  background-color: #3F3F3F;
  font-size: 35px;
  font-family: 'Pathway Gothic One', sans-serif;
  
}

.btn-bar{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}


.main-section{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}


.People:hover{
  -webkit-animation: pulse 5s infinite;
          animation: pulse 5s infinite;

}

@-webkit-keyframes pulse {
  0% {
    background-color: white;
  }
  50% {
    background-color: #76D7F2;
    color: white;
  }
  100%{
    background-color:  white;
  }
}

@keyframes pulse {
  0% {
    background-color: white;
  }
  50% {
    background-color: #76D7F2;
    color: white;
  }
  100%{
    background-color:  white;
  }
}

.People:focus{
  background-color: #76D7F2;
  color: white;
}


